const createHMACget = Symbol('createHMACget');      // see foot note EOF [2]
const createHMACother = Symbol('createHMACother');
import axios from 'axios';
import CryptoJS from 'crypto-js'
import Symbol from 'es6-symbol'
import {APIError as APIErr}  from './errors.js'
import {AuthenticationError}  from './errors.js'

class Protocols {
    constructor(endpoint, path_) {
        this.endpoint_ = endpoint;
        if (typeof(endpoint) === 'undefined' || !endpoint) {
            var endPointErrMsg = "Please pass in a valid endpoint - eg https://localhost:443 ";
            console.error(endPointErrMsg);
            console.warn("abbending program and exiting");
            return;
        }
        else {
            console.info(`endpoint supplied as ${endpoint}`);
        }
        this.defaultpath = '';
        if (typeof(path_) === 'undefined' || !path_) {
            console.log(`path not provided -- defaulting to ${this.defaultpath}`);
            this.path1 = this.defaultpath;
        }
        else {

            this.path1 = path_;
            console.log(`User defined path passed in is: ${path_ }`);
        }
    }
    makeCredentials(credentials) {
        this.credentials = {
            username: credentials.username,
            secret: CryptoJS.MD5(credentials.password).toString()
        };
    }
    //[3] - Calling Symbol -- expression is the name of the [ fxn ]
    [createHMACget](path) {
        if (!this.credentials || !this.credentials.secret || !this.credentials.username) {
            var errMsg = "Please authenticate() before calling other API values";
            console.warn(this.credentials);
            console.error(errMsg);
            throw new AuthenticationError({message: errMsg});
        }
        else {
            var hmac = CryptoJS.algo.HMAC.create(CryptoJS.algo.SHA256, this.credentials.secret);
            hmac.update(path);
            // var sig_ = `applicationname ${this.credentials.username}:${hmac.finalize()}`;
            // console.log(`creating hmac for ${this.credentials.username} on path ${path}`);
            // console.log(sig_);
            return `applicationname ${this.credentials.username}:${hmac.finalize()}`;
        }
    }

    [createHMACother](path, body) {
        //if(typeof body === 'undefined') {
        //    console.error("body  is not optional, please pass it to this function :: createHMACother() ")
        //    return;
        //}

        if (!this.credentials || !this.credentials.secret || !this.credentials.username) {
            var errMsg = "Please authenticate() before calling other API values";
            console.warn(this.credentials);
            console.error(errMsg);
            throw new AuthenticationError({message: errMsg});
        }
        else {
            var hmac = CryptoJS.algo.HMAC.create(CryptoJS.algo.SHA256, this.credentials.secret);
            hmac.update(path);

            if (typeof body !== 'undefined') {
                hmac.update(JSON.stringify(body));
            }

            // var sig_ = `applicationname ${this.credentials.username}:${hmac.finalize()}`;
            // console.log(`creating hmac for ${this.credentials.username} on path ${path}`);
            // console.log(sig_);
            return `applicationname ${this.credentials.username}:${hmac.finalize()}`;
        }
    }

    _getPath(action) {
        if (!this.path1) {
            return `/${action}`;
        } else {
            return `/${this.path1}/${action}`;
        }
    }

    /**
     * Handle api call promise resolve and reject.
     *
     * @param promise {axios.Promise} Promise object.
     * @returns {*|Promise.<T>|Promise|axios.Promise}
     */
    apiPromise(promise) {
        return promise.then((response) => response.data
        ).catch((error) => Promise.reject(new APIErr({
            'status': error.status,
            'statusText': error.statusText,
            'message': error.data.message
        })));
    }

    /**
     * Handle api get request.
     *
     * @param path {string} Path of api call without endpoint.
     * @returns {*|Promise.<T>|Promise|axios.Promise}
     */
    apiGet(path) {
        var url = `${this.endpoint_}${path}`;

        return this.apiPromise(axios.get(url, {
                headers: {Authorization: this[createHMACget](path)}
            }
        ));
    }

    /**
     * Handle api get request bypassing authentication
     *
     * @param path {string} Path of api call without endpoint.
     * @returns {*|Promise.<T>|Promise|axios.Promise}
     */
    apiGetNoAuth(path) {
        var url = `${this.endpoint_}${path}`;

        return this.apiPromise(axios.get(url));
    }

    /**
     * Handle api delete request.
     *
     * @param path {string} Path of api call without endpoint.
     * @returns {*|Promise.<T>|Promise|axios.Promise}
     */
    apiDelete(path) {
        var url = `${this.endpoint_}${path}`;

        return this.apiPromise(axios.delete(url, {
                headers: {Authorization: this[createHMACget](path)}
            }
        ));
    }

    /**
     * Handle api post request.
     *
     * @param path {string} Path of api call without endpoint.
     * @param body {object} Body object contains other non-string info..
     * @returns {*|Promise.<T>|Promise|axios.Promise}
     */
    apiPost(path, body) {
        var url = `${this.endpoint_}${path}`;

        return this.apiPromise(axios.post(url, body, {
                headers: {
                    Authorization: this[createHMACother](path, body)
                }
            }
        ));
    }

    /**
     * Handle api post request bypassing authentication.
     *
     * @param path {string} Path of api call without endpoint.
     * @param body {object} Body object contains other non-string info..
     * @returns {*|Promise.<T>|Promise|axios.Promise}
     */
    apiPostNoAuth(path, body) {
        var url = `${this.endpoint_}${path}`;

        return this.apiPromise(axios.post(url, body));
    }

    /**
     * Handle api put request.
     *
     * @param path {string} Path of api call without endpoint.
     * @param body {object} Body object contains other non-string info..
     * @returns {*|Promise.<T>|Promise|axios.Promise}
     */
    apiPut(path, body) {
        var url = `${this.endpoint_}${path}`;

        return this.apiPromise(axios.put(url, body, {
                headers: {
                    Authorization: this[createHMACother](path, body)
                }
            }
        ));
    }
}
export default Protocols;


