    /**
     * lookup inputs from astro_data and gives the specific fields
     * @param nodeID {string} Tree node Id.
     * @param selectedInputKeys {Array} - these keys are used to get matching input values
     * @param selectedOutputKeys {Array} - these keys are used to get matching output values
     * @returns list for fields to be displayed in universal output
     */
    getFieldList(nodeID, selectedInputKeys, selectedOutputKeys) {
        var path_ = this.protocols._getPath('domain/universal/io/fields');
        if (selectedInputKeys.length && selectedOutputKeys.length) {
            path_ = `${path_}/${nodeID}/io/${selectedInputKeys}/${selectedOutputKeys}`;
        } else if (selectedInputKeys.length) {
            path_ = `${path_}/${nodeID}/input/${selectedInputKeys}/`;
        } else if (selectedOutputKeys.length) {
            path_ = `${path_}/${nodeID}/output/${selectedOutputKeys}/`;
        } else {
            throw "Not enough keys.";
        }

        return this.protocols.apiGet(path_);
    }


